/* Topbar */
.topbar {
  background: #031321;
  display: flex;
  align-items: center;
}

.topbar-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  color: #2196f3;
  font-size: 25px;
  font-family: "Dancing Script", cursive;
}

.topbar-buttons {
  width: 50%;
}

.topbar-buttons ul {
  display: flex;
}

.topbar-buttons ul li {
  padding: 20px;
}

.topbar-buttons ul li .anchor {
  color: white;
  text-decoration: none;
  position: relative;
  display: inline-block;
  padding: 15px 30px;
  color: #2196f3;
  text-transform: uppercase;
  letter-spacing: 4px;
  text-decoration: none;
  font-size: 16px;
  overflow: hidden;
  transition: 0.2s;
}

.topbar-buttons ul li .anchor:hover {
  color: #255784;
  background: #2196f3;
  box-shadow: 0 0 10px #2196f3, 0 0 40px #2196f3, 0 0 80px#2196f3;
  transition-delay: 1s;
}

.topbar-buttons ul li .anchor span {
  position: absolute;
  display: block;
}

.topbar-buttons ul li .anchor span:nth-child(1) {
  top: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #2196f3);
}

.topbar-buttons ul li .anchor:hover span:nth-child(1) {
  left: 100%;
  transition: 1s;
}
.topbar-buttons ul li .anchor span:nth-child(3) {
  bottom: 0;
  right: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg, transparent, #2196f3);
}

.topbar-buttons ul li .anchor:hover span:nth-child(3) {
  right: 100%;
  transition: 1s;
  transition-delay: 0.5s;
}
.topbar-buttons ul li .anchor span:nth-child(2) {
  bottom: -100%;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg, transparent, #2196f3);
}

.topbar-buttons ul li .anchor:hover span:nth-child(2) {
  bottom: 100%;
  transition: 1s;
  transition-delay: 0.75s;
}
.topbar-buttons ul li .anchor span:nth-child(4) {
  top: -100%;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg, transparent, #2196f3);
}

.topbar-buttons ul li .anchor:hover span:nth-child(4) {
  top: 100%;
  transition: 1s;
  transition-delay: 0.25s;
}
